import axios from 'axios'
import * as types from '../store/mutation-types'
// axios.defaults.baseURL = 'http://192.168.0.240:3000/v1/';
axios.defaults.baseURL = 'https://www.oxotoken.cn/v1/';
axios.defaults.timeout = 5000;
axios.interceptors.request.use(
  config => {
    vue.$store.commit(types.LOADDING,true);
    if (sessionStorage.getItem('token')) config.headers.Token = sessionStorage.getItem('token');
    return config
  }, error => Promise.reject(error)
);
axios.interceptors.response.use(response => {
  vue.$store.commit(types.LOADDING,false);
  if(response.data.success===0){
    switch (response.data.code) {
      case 1:
        vue.$Message.error('请使用推广链接进行注册');
        vue.$store.commit(types.UPDATE_USER, "");
        break;
      default:
        break;
    }
  }else{
    return  response.data
  }
});

// 登录
export function login(data) {return new  Promise(resolve => axios.post('tryLogin', data).then(result => resolve(result)))}
// 用户信息
export function getUser(){return new Promise(resolve => {axios.post('getUser').then(result=>resolve(result))})}
// 系统信息
export function getSys(){return new Promise(resolve => axios.post('getSys').then((result)=>resolve(result)))}
// 获取计划
export function getPlan(){return new Promise(resolve => axios.post('getPlan').then((result)=>resolve(result)))}
// 保存交易哈希
export function saveHash(data) {return new Promise( resolve => axios.post('saveHash',data).then(result=>resolve(result)))}
// 确认哈希结果
export function confirmHash(data) {return new Promise(resolve => axios.post('confirmHash',data).then(result=>resolve(result)))}



